<template>
    <div class="intro-y box">
        <div class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
            <h2 class="font-medium text-base mr-auto">
                Add User Form
            </h2>
        </div>
        <div id="vertical-form" class="p-5">
            <div class="preview">
                <form v-on:submit.prevent="onSubmit" method="post">
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">First Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="First Name" required
                            v-model="posts.firstName" name="firstName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Last Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Last Name" required
                            v-model="posts.lastName" name="lastName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Email</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="email@gmail.com" required
                            v-model="posts.email" @blur="checkEmail">
                        <span v-if="emailError" class="error">{{ emailError }}</span>

                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Mobile Number</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Mobile Number" required
                            v-model="posts.phoneNumber" name="phoneNumber">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Password</label>
                        <input id="vertical-form-2" type="pwd" class="form-control" placeholder="password" required
                            v-model="posts.password" name="password">
                    </div>
                    <button class="btn btn-primary mt-5" type="submit">Add</button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import {
    postRequest
} from '../../pages/ApiFunctions';

export default {
    name: 'AddUser',
    data() {
        return {
            emailError: '',
            posts: {
                firstName: "",
                lastName: "",
                email: "",
                phoneNumber: "",
                password: "",

            }
        }
    },
    methods: {
        async onSubmit() {
            const response = await postRequest('/register', this.posts)
            console.log("rEsponsce=>",response);
            if (response.error) {
                this.emailError = response.error;
            } else {
                this.emailError = '';
                this.$router.push({
                    path: '/admin/user-data-list',
                    name:'UserDataList'
                })
            }

        },
    }
}
</script>



